function binarySearch(arr, val){
    let guess;
    let min = 0;
    let max = arr.length-1;
    while(min < max){
        guess = Math.floor(min + max);
        if(arr[guess] === val){
            return guess;
        }else if(val < arr[guess]){
            min = guess + 1;
        }else{
            max = guess - 1;
        }
    }
    return -1;
}

const arr = [8, 87, 3, 0, -12, 88, 4351, 0.7];

import {test} from 'ava'
test(t=>t.is(binarySearch(arr, 0), 3))
